body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-size: 18px;
}


.App {
  background: -webkit-gradient(linear, 
  left top, right top, 
  from(#0f0c29)
,
color-stop(#302b63)
,
to(#24243e)
  );
  background: linear-gradient(to right, 
  #0f0c29
,
#302b63
,
#24243e
  )
}

header {
  width: 100%;
  margin: auto;
  height: 8vh;
  color: white;
}

nav {
  display: flex;
  height: 8vh;
  align-items: center;
  justify-content: center;
}


.name {
  flex: 4 1;
  font-family: Arial, Helvetica, sans-serif;
  padding-left: 60px;
}
 
h1 {
  font-size: 20px;
}
  
.name:hover {
  color: #D1D1D9;
}


ul {
  flex: 1 1;
  display: flex;
  list-style-type: none;
  margin: 0px;
}

li {
  flex: 1 1;
  font-family: Arial, Helvetica, sans-serif;
}

li:hover {
  color: #D1D1D9;

}

.stickyHeader {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  background: -webkit-gradient(linear,right top, left top,from(#434343) , to(#000000));
  background: linear-gradient(to left,#434343 , #000000);
  z-index: 1
}

@media screen and (max-width: 500px){
  header {
    width: 100%;
    margin: auto;
    height: 12vh;
    color: white;
  }

  nav {
    display: flex;
    height: 12vh;
    align-items: center;
    justify-content: center;
  }

  .name {
    color: white;
    flex: 3 1;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    padding-left: 0px;
    text-align: center;
  }

  li {
    display: none;
  }

}
.main {
  width: 100%;
  margin: auto;
  color: white;
  text-align: center;
  height: 92vh;
}

.stone {
  position: relative;
  width: 100%;
  height: 90%;
}

.stone img{
  opacity: 0.8;
  width: 100%;
  height: 90%;
  object-fit: cover;
}

.headline {
  position: absolute;
  top: 95%;
  left: 5%;
  font-size: 40px;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

img {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
}

@-webkit-keyframes slidein {
  0% {
    opacity: 0.2;
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
  }
  100% {
    opacity: 0.8;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes slidein {
  0% {
    opacity: 0.2;
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
  }
  100% {
    opacity: 0.8;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@media screen and (max-width: 500px){
  .main {
    width: 100%;
    margin: auto;
    color: white;
    text-align: center;
    height: 88vh;
  }

  .headline {
    position: absolute;
    top: 95%;
    left: 5%;
    font-size: 30px;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
}
